import { Component, OnInit, ViewChild } from '@angular/core';
import { ModalDirective } from '../../../../../ng-uikit-pro-standard';
import { CommonService } from '../../../services/common.service';
import { ValidationsService } from '../../../services/validations.service';
// import { HrindexService } from '../../../admin/hrindex/hrindex.service';
import { CmsService } from '../../services/cms.service';

@Component({
  selector: 'app-hrindex',
  templateUrl: './hrindex.component.html',
  styleUrls: ['./hrindex.component.scss']
})
export class HRIndexComponent implements OnInit {

  public name: String = '';
  public email: String = '';
  public mobile: String = '';
  public designation: String = '';
  public companyname: String = '';
  public industry: String = '';
  public hrindexid: String = '';

  public question: any[] = [];
  public section: any[] = [];
  public staffing: any;
  public max: any;

  public options: Object;

  public questions: any;

  ngOnInit() {
    this.getQuest();
  }

  constructor(
    public _validationsService: ValidationsService,
    public _commonService: CommonService,
    private hrindexService: CmsService,
  ) {

  }

  getQuest() {
    this.hrindexService.getHrindexQuestions()
      .subscribe(res => {
        this.questions = res;
        let j = 0;
        for (const prop of this.questions) {
          this.question[j] = [];
          (j === 0) ? this.section[j] = true : this.section[j] = false;
          let i = 0;
          for (const props of prop.question) {
            this.question[j][i] = '';
            i++;
          }
          j++;
        }
        console.log(this.questions.length);
      });
  }



  @ViewChild('autoShownModal') public autoShownModal: ModalDirective;
  public isModalShownFirst: Boolean = false;
  public isModalShownSecont: Boolean = false;
  public isModalShownThired: Boolean = false;
  public isModalShownFourth: Boolean = false;
  public activeclass: any[] = [
    'active',
    '',
    '',
    '',
    '',
    '',
    '',
  ];
  

  getStaffing() {
    this.options = {
      chart: {
        type: 'column'
      },
      title: {
        text: this.title_values
      },
      subtitle: {
        text: ''
      },
      xAxis: {
        categories: [
          'Staffing',
          'Training & Development',
          'Performance Systems',
          'Safety and Health',
          'Labour Relations',
          'Internal Communication',
          'Diversity'],
        crosshair: true
      },
      yAxis: {
        min: 0,
        max: this.max_value,
        allowDecimals: true,
        plotLines: [{
          value: this.min_percentage_value,
          color: 'green',
          dashStyle: 'shortdash',
          width: 2,
          /*label: {
            text: this.minimum_text_label
          }*/
        }, {
          value: this.max_percentage_value,
          color: 'red',
          dashStyle: 'shortdash',
          width: 2,
          /*label: {
            text: this.maximum_text_label
          }*/
        }]
      },
      tooltip: {
        headerFormat: '<span style="font-size:10px">{point.key}</span><table>',
        pointFormat: '<tr><td style="color:{series.color};padding:0">{series.name}: </td>' +
          '<td style="padding:0"><b>{point.y:.1f}</b></td></tr>',
        footerFormat: '</table>',
        shared: true,
        useHTML: true
      },
      plotOptions: {
        column: {
          pointPadding: 0.2,
          borderWidth: 0
        }
      },
      series: [{
        name: 'Score',
        data: this.staffing
      }]
    };
  }

  public percentage: number;
  public max_value: any;
  public max_percentage_value: any;
  public min_percentage_value: any;
  public maximum_text_label: any;
  public minimum_text_label: any;
  public title_values: any;
  // public linevalue: String;
  staffingForm() {
    const k: any = [];
    const maa: any = [];
    const percentage: any = [];

    for (let j = 0; j < this.question.length; j++) {
      let m = 0;
      let ma = 0;
      let percentile = 0;
      for (let i = 0; i < this.question[j].length; i++) {
        if (this._validationsService.isEmpty(this.question[j][i])) {
          this._commonService.showMessage('error', 'Answer all Questions!');
          return false;
        }
        console.log(this.question[j][i].length);
        m = m + parseInt(this.question[j][i], 10);
        ma = ma + 4;
        percentile = (m / ma) * 100;
      }
      k.push(m);
      maa.push(ma);
      percentage.push(percentile);
    }
    //console.log(this.question.length);
    this.staffing = k;
    this.max = maa;
    let maximum_value = Math.max.apply(Math, this.max);
    this.max_value = maximum_value;
    let max_percentage = 80;
    let min_percentage = 60;
    let maximum_percentage = (80 * maximum_value) / 100;
    // console.log(Math.max.apply(Math, this.max));
    // console.log(maximum_percentage);
    this.max_percentage_value = maximum_percentage;
    this.maximum_text_label = "Max Line (" + max_percentage + "%) - Score (" + this.max_percentage_value + ")";
    //console.log(this.maximum_text_label);
    let minimum_percentage = (60 * maximum_value) / 100;
    // console.log(Math.min.apply(Math, this.max));
    // console.log(minimum_percentage);
    this.min_percentage_value = minimum_percentage;
    this.minimum_text_label = "Min Line (" + min_percentage + "%) - Score (" + this.min_percentage_value + ").360 EPES will help you in Augmenting Performance Possibilities";

    this.title_values = this.maximum_text_label + "---" + this.minimum_text_label;

    this.percentage = percentage;
    // let a = this.staffing.toString();
    // let b = this.percentage.toString();
    // this.linevalue = a + " ("+b+")";
    // console.log(Math.max(this.percentage));
    this.getStaffing();
    this.openFourth();
  }

  public showModal(modal: any): void {
    if (modal === 'f') {
      this.isModalShownFirst = true;
    } else if (modal === 's') {
      this.isModalShownSecont = true;
    } else if (modal === 't') {
      this.isModalShownThired = true;
    } else {
      this.isModalShownFourth = true;
    }
  }

  public hideModal(): void {
    this.autoShownModal.hide();
  }

  public onHidden(modal: any): void {
    if (modal === 'f') {
      this.isModalShownFirst = false;
    } else if (modal === 's') {
      this.isModalShownSecont = false;
    } else if (modal === 't') {
      this.isModalShownThired = false;
    } else if (modal === 'fo') {
      this.isModalShownFourth = false;
    }
  }

  openSecond() {
    this.onHidden('f');
    this.onHidden('t');
    this.showModal('s');
    this.onHidden('fo');
  }

  openThird() {
    this.onHidden('f');
    this.onHidden('s');
    this.showModal('t');
    this.onHidden('fo');
  }

  openFourth() {
    this.onHidden('f');
    this.onHidden('s');
    this.onHidden('t');
    this.showModal('fo');
    this.updatehrIndexGraph();
  }

  closeFourth() {
    this.onHidden('fo');
  }

  updatehrIndexGraph() {
    const fieldhrindexgraph = {
      companyname: this.companyname,
      staffing: this.staffing[0],
      traininganddevelopment: this.staffing[1],
      performancesystems: this.staffing[2],
      safetyandhealth: this.staffing[3],
      labourrelations: this.staffing[4],
      internalcommunication: this.staffing[5],
      diversity: this.staffing[6],
      id: this.hrindexid
    };
    this.hrindexService.addHrIndexValue(fieldhrindexgraph)
      .subscribe(res => {
        console.log(res);
        if (res.api_message == "success") {
          this._commonService.showMessage('success', res.api_message);
        } else {
          this._commonService.showMessage('error', res.api_message);
        }
      });
  }

  showNext(i: number) {

    for (let a = 0; a < this.question[i-1].length; a++) {
      if (this._validationsService.isEmpty(this.question[i-1][a])) {
        this._commonService.showMessage('error', 'Answer All Questions!');
        return false;
      }
    }
    //console.log(this.question[i-1].length);
    //console.log(this.questions[i-1].length);
    let j = 0;
    if (i === (this.questions.length)) {
      this.staffingForm();
      
      return false;
    }
    for (const prop of this.questions) {
      (j === i) ? this.section[j] = true : this.section[j] = false;
      (j <= i) ? this.activeclass[j] = 'active' : this.activeclass[j] = '';
      j++;
    }
  }

  hrIndexForm() {
    if (this._validationsService.isEmpty(this.name)) {
      this._commonService.showMessage('error', 'Name should not be empty!');
      return false;
    }
    if (this._validationsService.isEmpty(this.email)) {
      this._commonService.showMessage('error', 'Email should not be empty!');
      return false;
    }
    if (this._validationsService.isEmpty(this.mobile)) {
      this._commonService.showMessage('error', 'Mobile should not be empty!');
      return false;
    }
    if (this._validationsService.isEmpty(this.designation)) {
      this._commonService.showMessage('error', 'Designation should not be empty!');
      return false;
    }
    if (this._validationsService.isEmpty(this.companyname)) {
      this._commonService.showMessage('error', 'Company Name should not be empty!');
      return false;
    }
    if (this._validationsService.isEmpty(this.industry)) {
      this._commonService.showMessage('error', 'Industry should not be empty!');
      return false;
    }

    const hrindexField = {
      name: this.name,
      email: this.email,
      mobile: this.mobile,
      designation: this.designation,
      companyname: this.companyname,
      industry: this.industry,
    };

    this.hrindexService.addHrIndex(hrindexField)
      .subscribe(res => {
        this.openThird();
        if (res.api_message == "success") {
          this._commonService.showMessage('success', 'Successfully Saved');
          this.openThird();
        } else {
          this._commonService.showMessage('error', res.api_message);
        }
      });
  }
}
